


































































@import "@/assets/scss/colors.scss";
@import "@/assets/scss/variables.scss";
.addColumn{
  .dropdown{
    &.is-active{
      .button{
          border-bottom-left-radius: 0;
          border-bottom-right-radius: 0;
      }
    }
    .dropdown-trigger{
      .button{
        background: #F5F6F6;
        width: 280px;
        height: 49px;
        justify-content: left;
        outline: none;
        &:focus{
          border-color: transparent;
        }
        &:focus:not(:active){
          box-shadow: none;
        }
      }
    }
    .icon{
      svg{
        height: 15px;
      }
    }
  }
  .dropdown-menu{
    padding-top: 0px;
    .dropdown-content{
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }
    hr{
      width: 50%;
      transform: translateX(50%);
    }
    .dropdown-item{
      max-width: 280px;
      p{
        font-size: 12px;
      }
      p:first-child{
        font-size: 18px;
        svg{
          height: 15px;
        }
      }
    }
  }
}

