
































































































@import "@/assets/scss/variables.scss";
  .shortcuts{
      position: fixed;
      width: 100%;
      display: flex;
      align-items: stretch;
      flex-grow: 1;
      flex-shrink: 0;
      top: 7rem;
      padding: 0 2.5rem;
      .shortcuts-start{
          justify-content: flex-start;
          display: flex;

          select{
              background: transparent;
              border: none;
              color: $primary;
              outline: none;
              margin-left: 10px;
          }
      }
      .shortcuts-end{
          margin-left: auto;
          justify-content: flex-end;
          display: flex;

          .reset{
              color: $dark;
              text-decoration: underline;

              &:hover{
                  text-decoration: none;
              }
          }
          
          @media screen and (max-width: 800px) {
              display: none !important;
          }
      }
  }
