

























































































@import "@/assets/scss/variables.scss";
    .navbar{
        .navbar-brand{
            .navbar-item{
                img{
                    max-height: none;
                    width: 115px;
                }
                &:hover{
                    background: $primary-hovered;
                }
            }
        }

        .navbar-end{
            background: $dark;
            .navbar-item{
                text-transform: uppercase;    
                padding: 0.5rem 1.75rem;

                svg{
                    fill: $light;
                    height: 24px;
                    margin-right: 5px;
                }

                &:hover{
                    background-color: $dark-hovered !important;
                }

                &.is-hoverable{
                    &:hover{
                        .navbar-link{
                            background: $dark-hovered !important;
                        }
                    }

                    &::after{
                        content: '';
                        position: absolute;
                        width: 1px;
                        height: 50%;
                        background: $white;
                        top: 50%;
                        left: 0;
                        transform: translateY(-50%);
                    }
                }

                .navbar-dropdown{
                    background: $dark-hovered;
                    border: $dark-hovered;
                    a{
                        color: white;

                        &:hover{
                            background: $dark !important;
                        }
                    }
                }
            }
        }
    }
