$input-height : 59px;
button {
  &.margin-left-auto {
    margin-left: auto;
  }
  &.margin-right-auto {
    margin-right: auto;
  }
}
header {
  position: relative;
  padding: 0px 17px 0px 12px;
  .actions {
    display: flex;
  }

  &.search-params{
    .header{
      align-items: center;
      background: $light;
      padding: 0 .5rem;
      height: 49px;
      display: flex;
      font-size: 18px;
      font-weight: 600;
      border: 1px solid #dbdbdb;
      border-radius: 0;
      svg{
        height: 22px;
        fill: $dark;
      }

      .icon{
        margin-right: 7px;
      }
      .search-input{
        background: transparent;
        outline: none;
        margin-bottom: 0;
      }

      .settings{
        justify-content: flex-end;
        margin-left: auto;
        cursor: pointer;
        transform: translateY(5px);
      }
    }

    .filters{
      &:focus {
          outline: none;
      }
      .field{
        margin-bottom: 0;
        height: $input-height;
        .control{
          height: $input-height;
          .select{
              height: $input-height;
            &:not(.is-multiple):not(.is-loading)::after {
                border-color: #757575;
                right: 1.125em;
                z-index: 4;
            }
            &:not(.is-multiple):not(.is-loading)::after, .navbar-link:not(.is-arrowless)::after {
              border-width: 2px;
            }
            select{
              color: $primary;
              font-weight: 600;
              height: $input-height;
              border-radius: 0;
            }
          }
          .icon{
            transform: translateY(9px);
            svg{
              height: 22px;
              fill: #757575;
            }
          }
        }
      }
      .button{
        height: $input-height;
        color: $danger;
        font-weight: 600;
        justify-content: left;
        border-radius: 0;
        .icon{
          svg{
            height: 22px;
            fill: $danger;
          }
        }
      }
      .move-column{
        height: $input-height;
        display: flex;
        background: $light;
        border: 1px solid #dbdbdb;
        border-radius: 0;
        padding-left: 5px;
        .btn-icon{
          padding: 5px;
          svg{
            height: 22px;
            fill: #757575;
          }
        }
      }
    }
  }
}
